import { useState, useEffect, useRef } from "react";
import { Panel, PanelGroup, PanelResizeHandle } from "react-resizable-panels";
import BlockEditor from "../components/BlockEditor";
import blocksToHTML from "../utils/blocksToHTML";
import { initialBlocks } from "../data/initialCV";
import { Block } from "../utils/types";

// Styles CSS globaux + responsivité
const styles = `
  :root {
    --bg: #f7f8fb;
    --card: #ffffff;
    --border: #e7eaf0;
    --muted: #6b7280;
    --text: #111827;
    --primary: #2563eb;
    --primary-600: #2f6ded;
    --ring: rgba(37, 99, 235, 0.25);
  }

  * { box-sizing: border-box; }
  html, body, #__next { height: 100%; }
  body {
    margin: 0;
    background: var(--bg);
    color: var(--text);
    font-family: Inter, ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    letter-spacing: 0.1px;
  }

  button {
    transition: background-color .15s ease, box-shadow .15s ease, transform .06s ease;
  }
  button:active { transform: translateY(1px); }

  .card {
    background: var(--card);
    border: 1px solid var(--border);
    border-radius: 12px;
    box-shadow: 0 1px 2px rgba(0,0,0,0.04), 0 8px 24px rgba(17,24,39,0.04);
  }

  .muted { color: var(--muted); }

  .preview-cv {
    transform: scale(0.8);
    transform-origin: top center;
  }
  
  @media (max-width: 1200px) {
    .preview-cv { transform: scale(0.6) !important; }
  }
  
  @media (max-width: 768px) {
    .preview-cv { transform: scale(0.5) !important; }
  }
`;

export default function Home() {
  // IDs stables: charger depuis localStorage si disponible
  const [blocks, setBlocks] = useState<Block[]>([]);
  const [fontScale, setFontScale] = useState<number>(1);
  const [showWarning, setShowWarning] = useState<boolean>(false);
  const previewRef = useRef<HTMLDivElement>(null);

  // Charger/Sauvegarder les blocs pour stabiliser les IDs (éviter HMR qui regénère)
  useEffect(() => {
    if (typeof window === "undefined") return;
    try {
      const stored = window.localStorage.getItem("cv_blocks");
      if (stored) {
        const parsed = JSON.parse(stored) as Block[];
        setBlocks(parsed);
      } else {
        setBlocks(initialBlocks);
        window.localStorage.setItem("cv_blocks", JSON.stringify(initialBlocks));
      }
    } catch (e) {
      setBlocks(initialBlocks);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (typeof window === "undefined") return;
    try {
      window.localStorage.setItem("cv_blocks", JSON.stringify(blocks));
    } catch {}
  }, [blocks]);

  // Fonction pour calculer la taille de police optimale
  const calculateOptimalFontScale = () => {
    if (!previewRef.current) return 1;
  
    // Hauteur maximale A4 approximative en pixels
    const maxHeight = 1050;
    let scale = 1;
  
    // Créer un div temporaire pour mesurer
    const tempDiv = document.createElement("div");
    tempDiv.style.width = "210mm";
    tempDiv.style.padding = "15mm";
    tempDiv.style.boxSizing = "border-box";
    tempDiv.style.position = "absolute";
    tempDiv.style.visibility = "hidden";
    document.body.appendChild(tempDiv);
  
    // Itérer pour trouver la plus grande taille qui tient
    let currentScale = 1;
    let step = 0.02; // petit pas pour précision
    while (currentScale > 0.5) {
      tempDiv.style.fontSize = `${currentScale}em`;
      tempDiv.style.lineHeight = `${1.1 * currentScale}`;
      tempDiv.innerHTML = blocksToHTML(blocks, currentScale);
      const contentHeight = tempDiv.scrollHeight;
  
      if (contentHeight <= maxHeight) {
        scale = currentScale;
        break;
      }
      currentScale -= step;
    }
  
    document.body.removeChild(tempDiv);
    return scale;
  };
  

  // Vérifier le dépassement à chaque changement de blocks avec délai
  useEffect(() => {
    const timer = setTimeout(() => {
      const scale = calculateOptimalFontScale();
      setFontScale(scale);
      setShowWarning(scale < 1);
    }, 100); // Délai pour laisser le DOM se mettre à jour
    
    return () => clearTimeout(timer);
  }, [blocks]);

  const handleGeneratePDF = async () => {
    const res = await fetch("/api/generate-pdf", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ blocks, fontScale }),
    });
    const blob = await res.blob();
    
    // Créer un lien de téléchargement
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'mon-cv.pdf';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  return (
    <>
      <style>{styles}</style>
      <PanelGroup direction="horizontal" style={{ height: "100vh" }}>
        {/* Partie gauche - Éditeur (scrollable) */}
        <Panel defaultSize={60} minSize={30} maxSize={80}>
          <div style={{ 
            height: "100%", 
            padding: "1rem",
            overflow: "auto"
          }}>
            <BlockEditor blocks={blocks} setBlocks={setBlocks} />
          </div>
        </Panel>
        
        {/* Séparateur redimensionnable */}
        <PanelResizeHandle 
          style={{
            width: "10px",
            background: "linear-gradient(180deg, #eef1f6 0%, #e7eaf0 100%)",
            cursor: "col-resize",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            borderLeft: "1px solid #e6e9ef",
            borderRight: "1px solid #e6e9ef"
          }}
        >
          <div style={{
            width: "4px",
            height: "36px",
            background: "#c9d1e1",
            borderRadius: "2px"
          }} />
        </PanelResizeHandle>
        
        {/* Partie droite - Aperçu (fixe) */}
        <Panel defaultSize={45} minSize={45} maxSize={45}>
          <div style={{ 
            height: "100vh",
            overflow: "auto",
            backgroundColor: "var(--bg)",
            padding: "0.75rem",
            borderLeft: "1px solid var(--border)"
          }}>
        {showWarning && (
          <div style={{
            backgroundColor: "#fff7e6",
            border: "1px solid #ffe2a8",
            borderRadius: "8px",
            padding: "10px 12px",
            margin: "10px 0",
            color: "#8a6d3b",
            fontSize: "14px"
          }}>
            ⚠️ Attention : Tu dépasses la première page ! La taille de police a été réduite à {Math.round(fontScale * 100)}% pour tenir sur une page.
          </div>
        )}
        
        <div style={{ 
        }}>
          <div
            ref={previewRef}
            className="preview-cv"
            style={{
              width: "210mm",
              minHeight: "297mm",
              padding: "15mm",
              border: "1px solid #d1d5db",
              boxSizing: "border-box",
              backgroundColor: "#fff",
              fontSize: `${fontScale}em`,
              lineHeight: `${1.1 * fontScale}`,
              transform: "scale(0.8)",
              transformOrigin: "top left",
              marginBottom: "0.1rem",
              borderRadius: "8px",
              boxShadow: "0 12px 24px rgba(17,24,39,0.06)"
            }}
            dangerouslySetInnerHTML={{ __html: blocksToHTML(blocks, fontScale) }}
          />
        </div>
        
        <div style={{ 
          display: "flex", 
          flexDirection: "column",
          gap: "8px",
          position: "sticky",
          bottom: "0.5rem",
          backgroundColor: "#fff",
          padding: "1rem",
          borderRadius: "12px",
          border: "1px solid var(--border)",
          boxShadow: "0 1px 2px rgba(0,0,0,0.04), 0 8px 24px rgba(17,24,39,0.06)"
        }}>
          <button 
            onClick={handleGeneratePDF}
            style={{
              padding: "12px 16px",
              backgroundColor: "var(--primary)",
              color: "white",
              border: "1px solid transparent",
              borderRadius: "10px",
              cursor: "pointer",
              fontSize: "14px",
              fontWeight: "600",
              boxShadow: "0 1px 2px rgba(0,0,0,0.05)",
              outline: "none"
            }}
          >
            Générer PDF
          </button>
          <a
            href="/landing"
            style={{
              textAlign: "center",
              padding: "10px 12px",
              backgroundColor: "#fff",
              color: "#111827",
              border: "1px solid var(--border)",
              borderRadius: "10px",
              textDecoration: "none",
              fontSize: "14px",
              fontWeight: 600
            }}
          >
            Voir la landing
          </a>
        </div>
        
          </div>
        </Panel>
      </PanelGroup>
    </>
  );
}
